 .text {
    font-family: 'Bungee Inline';
    font-size: 20px;
    transform: rotate(180deg);
    padding-bottom: 60px;
    color: black;
    margin-top: 0px;
}

@media (max-width: 800px) {
    .text { 
        font-size: 16px;
    }
}

@media (max-width: 500px) {
    .text { 
        font-size: 14px;
        padding-bottom: 40px;
    }
}