.bacon {
    background-image: url(bacon.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 53%;
    margin-left: 24%;
}
@media (max-width: 1000px) {
    .bacon{
        width: 70%;
        margin-left: 16%;
    }
}


@media (max-width: 700px) {
    .bacon{
        width: 100%;
        margin-left: 0px;
    }
}