.BreadTop_img{
    transform: rotate(180deg);
    width: 460px;
    height: 140px;
}
.BreadTop{ 
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

@media (max-width: 800px) {
    .BreadTop_img{
        width: 390px;
        height: 110px;
    }
}

@media (max-width: 500px) {
    .BreadTop_img{
        width: 340px;
        height: 100px;
    }
}

@media (max-width: 450px) {
    .BreadTop_img{
        width: 300px;
        height: 90px;
    }

}