.styleOrder{
    padding: 25px;
    padding-left: 30px;
    padding-right: 30px;
    font-weight: 700;
    font-size: 20px;
    border-width:5px ;
    border-style: solid;
    border-color:rgb(255, 230, 0) ;
    background-color: rgb(36, 44, 59);
    margin-left: 43%;
    border-radius: 16px;
    cursor: pointer;
    transition: all 0.1s 0s;
    color: blanchedalmond;
    margin: auto;  
    margin-top: 100px;

}

.styleOrder:hover{  
    background-color: rgb(255, 230, 0);
    color: rgb(255, 230, 0);
    padding-left: 35px;
    padding-right: 35px;
}



