.chesse_img{
    transform: rotate(180deg);
    width: 550px;
    height: 70px;
}

.chesse{
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    margin: 0px;
    width: 100%;
    height: 12px;
}

@media (max-width: 800px) {
    .chesse_img{
        width: 430px;
        height: 60px;
    }

}

@media (max-width: 500px) {
    .chesse_img{
        width: 380px;
        height: 55px;
    }

}

@media (max-width: 450px) {
    .chesse_img{
        width: 330px;
        height: 40px;
    }

}