.cheese {
    background-image: url(cheese.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 53%;
    margin-left: 24%;
}

@media (max-width: 1000px) {
    .cheese {
        width: 70%;
        margin-left: 16%;
    }
}

@media (max-width: 700px) {
    .cheese {
        width: 100%;
        margin-left: 0px;
        height: 300px;
    }
}