.Burger{
    width: 100%;
    margin: auto;
    height: 250px;
    overflow: scroll;
    text-align: center;
    font-weight: bold;
    font-size: 1.2rem;
    color: rgba(49, 52, 54, 0.815);
    transform: rotate(180deg);
}
::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #ffffff00;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: rgb(255, 179, 0);
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: rgb(255, 230, 0);
}

.Style {
    border-radius: 5px;
    border-color: rgb(180, 180, 180);
    float: left;
    width: 100%;
    height: 500px;
    border-width: 2px;
    text-align: center;
    font-family: 'Allerta Stencil';
    font-size: 20px;
}

@media (min-width: 500px)  {
    .Burger{
        width: 100%;
        height: 500px;
    }
}

@media (min-width: 800px)  {
    .Burger{
        width: 100%;
    }
}

@media (min-width: 1000px) {
    .Burger{
        width: 60%;
    }
}

@media (min-width: 4000px) {
    .Burger{
        width: 60%;
    }
}
@media (max-width: 500px)  {
    .Burger{
        width: 100%;
        height: 500px;
    }
}