.BreadBottom_img{
    transform: rotate(180deg);
    width: 460px;
    height: 140px;
}
.BreadBottom{
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 80px;
}

@media (max-width: 800px) {
    .BreadBottom_img{
        width: 390px;
        height: 110px;
    }
    .BreadBottom{
        height: 65px;
    }
}

@media (max-width: 500px) {
    .BreadBottom_img{
        width: 340px;
        height: 100px;
    }
    .BreadBottom{
        height: 65px;
    }
}

@media (max-width: 450px) {
    .BreadBottom_img{
        width: 300px;
        height: 90px;
    }
    .BreadBottom{
        height: 55px;
    }
}