.salad {
    background-image: url(salad.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 53%;
    margin-left: 24%;
}

@media (max-width: 1000px) {
    .salad {
        width: 70%;
        margin-left: 16%;
    }
}

@media (max-width: 700px) {
    .salad {
        width: 100%;
        margin-left: 0px;
    }
}