.Meat_Img{
    transform: rotate(180deg);
    width: 460px;
    height: 110px;
}
.Meat{
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    margin: 0px;
    width: 100%;
    height: 50px;
    
    
}

@media (max-width: 800px) {
    .Meat_Img{
        width: 390px;
        height: 100px;
    }
    .Meat{
        height: 40px;
    }
}

@media (max-width: 500px) {
    .Meat_Img{
        width: 340px;
        height: 90px;
    }
    .Meat{
        height: 40px;
    }
}

@media (max-width: 450px) {
    .Meat_Img{
        width: 310px;
        height: 70px;
    }
    .Meat{
        height: 30px;
    }

}