.Salad_img{
    transform: rotate(180deg);
    width: 500px;
    height: 120px;
}
.Salad{
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 60px;
}

@media (max-width: 800px) {
    .Salad_img{
        width: 410px;
        height: 100px;
    }
    .Salad{
        height: 50px;
    }
}

@media (max-width: 500px) {
    .Salad_img{
        width: 360px;
        height: 90px;
    }
    .Salad{
        height: 50px;
    }
}

@media (max-width: 450px) {
    .Salad_img{
        width: 330px;
        height: 70px;
    }
    .Salad{
        height: 40px;
    }

}